const Noticia = (props) => {

    const {author, content, description,publishedAt,title, url ,urlToImage, source} = props.noticia;

    let image;
    if(urlToImage !== ''){
        image = <img src={urlToImage} alt={title} className="card-img-top" />
    }else{
        image = <p className="text-center my-5"> imagen no disponible </p>
    }
    return ( 
        <div className="col-md-6 col-12 mb-4">
            <div className="card">
                <div className="card-image">
                    {image}
                </div>
                <div className="card-body">
                    <div className="card-title">
                    {title}
                    </div>
                    <div className="card-text">
                    {description}
                    </div>
                    <div className="card-text">
                    {source.name}
                    </div>
                    <a href={url} target="_blank" className="btn btn-primary d-block"> leer noticia </a>
                </div>
            </div>
        
        </div>
    
    );
}
 
export default Noticia;